:root{--primary: blueviolet;--secondary: #46f2c3;--three: #ff2da4;--white: #ffffff}.blueviolet{color:blueviolet;color:var(--primary)}.bg-blueviolet{background-color:blueviolet;background-color:var(--primary)}.gray{color:darkgray}.head-container{position:fixed;width:100%;bottom:0}.head-container img{object-fit:contain;padding:12px;height:130px;padding-bottom:8px;-webkit-filter:brightness(0) invert(1);filter:brightness(0) invert(1)}.head-container img:hover{-webkit-filter:none;filter:none;transition-duration:350ms}.logo-container{position:fixed;left:16px;background-color:white}.logo-container img{object-fit:contain;height:80px;padding:8px;-webkit-filter:invert(1);filter:invert(1)}.sidetalk-container{position:absolute;right:-20px;bottom:332px;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;-webkit-text-orientation:mixed;text-orientation:mixed}*{padding:0;margin:0}body{font-family:'Raleway', sans-serif;letter-spacing:0.4px}p,blockquote,h1,h2,h3,h4,h5,pre,table{margin:24px 0}.uppercase{text-transform:uppercase}.underline{text-decoration:underline}i,em{color:blueviolet;color:var(--primary)}a{color:blueviolet;color:var(--primary);text-decoration:underline}b{font-weight:800;letter-spacing:0.8px}.violet{color:blueviolet;color:var(--primary)}p{font-size:10px;font-family:Raleway;font-size:10px;line-height:1.2;margin:8px 0}h1{font-weight:900;font-size:120px;letter-spacing:12px;line-height:1;margin-top:64px}h2{font-size:40px;line-height:1.3;letter-spacing:1px;font-weight:800;margin-top:80px}h3{font-size:20px;font-weight:800;line-height:1.2;letter-spacing:1px;text-transform:uppercase;margin-top:60px}h4{color:blueviolet;color:var(--primary);letter-spacing:1px;font-weight:500;line-height:1.6;text-transform:uppercase}h5{text-transform:uppercase;letter-spacing:4px;text-decoration:underline;line-height:2;font-weight:900}h6{text-transform:uppercase;letter-spacing:0.8px;font-weight:700;letter-spacing:1px;font-size:10px}blockquote{border-left:3px solid blueviolet;border-left:3px solid var(--primary);padding-left:24px;line-height:1.8;font-weight:300}.bq2{border-left:8px solid blueviolet;border-left:8px solid var(--primary);padding:24px;line-height:1.8;font-weight:600;background:#ececec;border-radius:8px}.stroked{-webkit-text-stroke:1px black;color:white;text-shadow:4px 4px 4px 4px #000}.frame{border:16px solid #46f2c3;height:100%;width:100%;box-sizing:border-box;position:fixed}.flex{display:-webkit-flex;display:flex}.grid-holder{margin:auto;position:relative}.grid-holder h1{text-align:center}.grid-holder .tiny{font-weight:800}.grid-holder .main-grid{position:relative;border-top:0 !important;border-left:0 !important}.grid-bg{padding:24px;overflow:scroll;height:-webkit-fill-available}.grid-bg .flex{-webkit-justify-content:center;justify-content:center;margin:auto;-webkit-flex-wrap:wrap;flex-wrap:wrap;box-sizing:content-box}.grid-bg .flex-between{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-end;align-items:flex-end}.grid-bg .flex{-webkit-justify-content:center;justify-content:center;margin:auto;-webkit-flex-wrap:wrap;flex-wrap:wrap;box-sizing:content-box}.grid-bg .flex-between{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-end;align-items:flex-end}.grid-bg .flex,.grid-bg .square,.grid-bg .square-larger{border-style:solid;box-sizing:border-box}.grid-bg .square{border-bottom:0 !important;border-right:0 !important}.grid-larger{position:absolute;top:0;left:0;border:none !important}.square-larger{background-color:white;position:relative;box-shadow:lightgray 8px 8px;background-size:cover;background-position:center center;background-repeat:no-repeat}.square-larger .tiny{position:absolute;top:-20px}.controls{position:fixed;top:24px;right:24px;text-align:right;background-color:white}.controls .controls-off{display:none}.controls h6{text-decoration:underline;cursor:point